





















































































import Vue from 'vue'
var json = require('@/static/content.json')

export default Vue.extend({
  name: 'project',
  data () {
    return {
      page: {},
      uri: this.$route.params.id,
      pageNotFound: false
    }
  },
  mounted () {
    json[this.uri] === undefined
      ? this.pageNotFound = true
      : this.page = json[this.uri]
  }
})
